/*-----------header-------------*/
.header{
	position: relative;
	background-color: #FAFAFA;
	overflow: hidden;
	outline: 1px solid;
	
	&:after{
		content: '';
		display: block;
		position: absolute;
		width: 928px;
		height: 100%;
		top: 0;
		left: 55%;
		background:  url(../img/header-bg.jpg) 0 0 no-repeat;
		background-size: cover;
		z-index: 0;
	}
	.header-inner{
		position: relative;
		z-index: 1;
	}
	.header-bottom{
		.wrapper{
			position: relative;
			justify-content: flex-end;
			padding-bottom: rem(138);

			.header-img{
				position: absolute;
			    left: 24%;
    			bottom: -161px;
				z-index: 1;
			}


			.header-bottom__left{
				@include col();
				@include col-9();
				@include col-lg-13();
				@include col-xs-18();
				padding-top: rem(93);
				position: relative;
				z-index: 2;

				h1{
					margin-bottom: rem(50);
					text-align: left;

					span{
						color: $accent;
					}
				}
				.sect-descr{
					margin-bottom: rem(117);
					span{
						font-family: $mon-b;

						text-transform: uppercase;
					}
				}
				.header-bottom__button{
					margin-bottom: rem(56);

					.button{
						position: relative;
					}
				}
			}

			.header-bottom__right{
				@include col();
				@include col-8();
				// @include col-lg_1734-13();
				padding-top: rem(178);
				padding-right: rem(40);
				text-align: right;

				.header-bottom-items{
					display: inline-block;
					position: relative;
					z-index: 0;
				}
				.header-bottom-item{
					width: 100%;	
					max-width: 422px;
					padding: 10px 10px 11px 20px;
					margin-bottom: 18px;
					display: -webkit-flex;
					display: -moz-flex;
					display: -ms-flex;
					display: -o-flex;
					display: flex;
					align-items: flex-start;
					border: 1px solid #FFFFFF;
					// box-shadow: 0px 10px 20px #D8D8D8;
					border-radius: 20px;
				}
				.header-bottom-item__num{
					margin-right: 30px;
					width: 48px;	
					position: relative;
					font-family: $mus-r;
					font-size: 72px;
					color: #fff;
					line-height: 72px;
					flex-shrink: 0;
					text-align: center;	
					

					&:after{
						content: '';
						display: block;
						width: 1px;
						height: 47px;
						position: absolute;
						top: 10px;
						right: -13px;
						background-color: #C4C4C4;
					}
				}

				.header-bottom-item__text{
					margin-top: 12px;
					text-align: left;
					font-size: 16px;
					line-height: 21px;
					color: #fff;
				}
			}
		}
	}


}
.header-top{
		position: relative;
		padding-top: 15px;
		z-index: 98;

		.header-menu{
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			background: radial-gradient(284.62px at 50% 52.08%, rgba(255, 255, 255, 0.91) 0%, rgba(255, 255, 255, 0.7) 100%), #26A3CC;
			z-index: 99;
			li{
				a{
					display: block;
					padding: 16px 30px 14px 30px;
					text-align: center;
					font-size: 24px;
					font-family: $mon-m;
					color: #000;
					text-decoration: none;
				}
			}
		}
		.header-menu__tel{
			display: block;
			position: relative;
			padding: 32px 30px;
			font-size: 24px;
			text-align: center;
			background-color: #fff;

			&:before{
				content: '';
				position: absolute;
				display: block;
				@include absolute-center;
				bottom: auto;
				top: 0;
				margin-left: 30px;
				margin-right: 30px;
				width: 100%;
				height: 1px;
				background-color: #E9E9E9;
			}

		}
		.wrapper{
			align-items: center;
			padding-right: rem(100);

			.header-top__left,
			.header-top__right{
				@include col();
				@include col-9();
				@include col-lg_1734-7();		
			}

			.header-top__left{
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: center;
				@include col-lg-8();
				@include col-mdr-9();
				@include col-sm-18();
			}
			
		}
		.header-callback-mob{
			display: none;
			margin-left: 30px;
			width: 36px;
			height: 36px;
			border-radius: 50%;
			background: linear-gradient(180deg, #FFAA30 0%, #FF8200 100%), #F76309;
		}

		&--fixed{
			padding-top: 10px;
			padding-bottom: 10px;
			background-color: #fff;

			.header-menu{
				display: none;
			}

			.logo a img
			{
				max-width: 150px;
			}

			.academy-contacts .academy-contacts__tel{
				color: #000;
			}
			.academy-contacts__callback{
				padding-top: 8px;
				padding-bottom: 8px;
				padding-left: 9px;
				background: linear-gradient(180deg, #FFAA30 0%, #FF8200 100%), #F76309;
				border: 1px solid #FFFFFF;

				.icon-svg{
					background: linear-gradient(180deg, rgba(255, 170, 48, 0.41) 0%, rgba(255, 130, 0, 0) 100%), #F76309;
				}

				&:hover{
					background: transparent;
					border-color: #000;
					color: #000;
					.icon-phone{
						background: transparent;
						border-color: #000;
						svg{
							fill: $accent;
						}
					}
				}
				&:active{
					.icon-phone{
						svg{
							fill: #000;
						}
					}
				}
			}
			
		}
}
.banner--clone{
	&:after{
		display: none;
		
	}
}
.header-top--fixed{
}

@media only screen and (max-width : 1764px){

	.header-top .wrapper,
	.header-top--fixed .wrapper{
		padding-right: 15px;
	}
	.logo a img {
		max-width: 128px;
	}
	.header-top .logo .logo-text{
		flex-shrink: 1;
		line-height: 16px;
	}
	.header .header-bottom .wrapper .header-img{
		max-height: 120%;
		left: 11%;
	}

}

@media only screen and (max-width : 1100px){
	.header .header-bottom .wrapper .header-bottom__right{
		display: none;
	}
	.header{
		&:after{
			display: none;
		}
	}
	.header .header-bottom .wrapper{
		justify-content: flex-start;
	}
	.header .header-bottom .wrapper .header-img{
		max-height: 130%;
		right: -10%;
		left: auto;
	}
	.academy-contacts .academy-contacts__tel{
		color: #000;
		font-size: 18px;
	}
	.academy-contacts__callback{
				padding-top: 8px;
				padding-bottom: 8px;
				padding-left: 9px;
				background: linear-gradient(180deg, #FFAA30 0%, #FF8200 100%), #F76309;
				border: 1px solid #FFFFFF;

				.icon-svg{
					background: linear-gradient(180deg, rgba(255, 170, 48, 0.41) 0%, rgba(255, 130, 0, 0) 100%), #F76309;
				}

				&:hover{
					background: transparent;
					border-color: #000;
					color: #000;
					.icon-phone{
						background: transparent;
						border-color: #000;
						svg{
							fill: $accent;
						}
					}
				}
				&:active{
					.icon-phone{
						svg{
							fill: #000;
						}
					}
				}
			}
	.logo{
		margin-left: 10px;
	}
	.logo a img{
		max-width: 100px;
	}
	.menu-icon{
		height: 30px;
		.dot {
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    width: 6px;
		    height: 6px;
		    background-color: #000;
		    border-radius: 10px;
		    @include transform(translate(-50%, -50%));
		    @include transition(margin .4s ease .4s, width .4s ease);
		    
		    &:nth-of-type(1) {
		      margin-top: -10px;
		      margin-left: -10px;
		      @include transform(translate(-50%, -50%) rotate(45deg));
		    }
		    
		    &:nth-of-type(2) {
		      margin-top: -10px;
		      @include transform(translate(-50%, -50%) rotate(-45deg));
		    }
		    
		    &:nth-of-type(3) {
		      margin-top: -10px;
		      margin-left: 10px;
		    }
		    
		    &:nth-of-type(4) {
		      margin-left: -10px;
		    }
		    
		    &:nth-of-type(5) {  
		      
		    }
		    
		    &:nth-of-type(6) {
		      margin-left: 10px;
		    }
		    
		    &:nth-of-type(7) {
		      margin-top: 10px;
		      margin-left: -10px;
		    }
		    
		    &:nth-of-type(8) {
		      margin-top: 10px;
		    }
		    
		    &:nth-of-type(9) {
		      margin-top: 10px;
		      margin-left: 10px;
		    }
		  }

		  &.clicked {
		  	.dot {

		  		&:nth-of-type(1) {
		  			width: 30px;
		  			margin-top: 0;
		  			margin-left: 0;
		  		}

		  		&:nth-of-type(2) {
		  			width: 30px;
		  			margin-top: 0;
		  			margin-left: 0;
		  		}
		  	}
		  }
	}
	.logo .logo-text div:last-child{
		font-size: 12px;
		line-height: 12px;
	}
	.logo .logo-text div:first-child{
		font-size: 20px;
		line-height: 20px;
	}
	.header .header-bottom .wrapper .header-img{
 		right: 0;
	}
}

@media only screen and (max-width : 992px){
	.academy-contacts .academy-contacts__callback{
		display: none;
	}
	.academy-contacts .header-callback-mob{
		display: inline-block;
	}
}

@media only screen and (max-width : 768px){
	.academy-contacts .academy-contacts__tel{
		display: none;
	}
	.header-top .wrapper .header-top__left{
		align-items: flex-start;
	}
	.header-top{
		.wrapper{
			position: relative;
		}
		.logo{
			max-width: 187px;
			margin-left: 0;
			display: block;
			text-align: center;
			margin-left: auto;
			margin-right: auto;
			a{
				display: block;
				img{
					max-width: 90px;
				}
			}
			.logo-text{
				margin-left: 0;
			}
		}

		.logo .logo-text div:first-child{
			font-size: 18px;
			line-height: 19px;
		}
	}
	.academy-contacts{
		width: auto;
		margin: 0;
		position: absolute;
		top: 0;
		right: 15px;
	}
	.menu-icon{
		width: 30px;
	}
	.header .header-bottom .wrapper .header-img{
    right: -150px;
    bottom: -30px;
    // max-height: 110%;
	}
}

@media only screen and (max-width : 560px){
	.header{
		overflow: hidden;
	}
	.header .header-bottom .wrapper .header-img{
    right: -200px;
    bottom: 0;
    max-height: 110%;
	}
}

@media only screen and (max-width : 480px){
	.header .header-bottom .wrapper{
		padding-bottom: 62px;
	}
	.header-bottom__left .header-bottom__button .parabol{
    right: -23px;
        margin-top: -58px;
		background-image: url(../img/icons-svg/parabol-wt.svg);
		background-size: 100%;
	}

	.header-bottom__left .academy-subtitle{
		color: $accent;
		text-align: center;
	}
	.header .header-bottom .wrapper .header-bottom__left .sect-descr{
			// max-width: 316px;
			margin-bottom: 200px;
			font-size: 24px;
			line-height: 30px;

			span{
				display: block;
			}
		}

	.header .header-bottom .wrapper .header-img{
  	right: -330px;
    bottom: 0;
    max-height: 110%;
	}
	

}
/*-----------end header-------------*/